import Vue from "vue";
import VueRouter from "vue-router";
import Content from "@/views/Content";
import Home from "@/views/Home";
import Artist from "@/views/Artist";
import HowDoesItWork from "@/views/HowDoesItWork";
import ForArtists from "@/views/ForArtists";
import Artists from "@/components/Artists";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Content,
    redirect: "/home",
    children: [
      {
        path: "/home",
        name: "Home",
        component: Home,
      },
      {
        path: "/hdiw",
        name: "HDIW",
        component: HowDoesItWork,
      },
      {
        path: "/forartists",
        name: "ForArtists",
        component: ForArtists,
      },
      {
        path: "/artists",
        name: "Artistas",
        redirect: "/artists/listing",
        component: Artist,
        children: [
          {
            path: "listing",
            name: "Artistas",
            component: Artists,
          },
        ],
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
